.card {
    width: 63.3vw;
    overflow: hidden;
    cursor: pointer;
    display: flex;
    align-items: center;
    height: calc(35px + (180 - 35) * ((100vw - 320px)/ (1440 - 320)));
    background: rgba(248, 248, 248, 0.7);
    box-shadow: 0px 0px 30px rgba(14, 17, 17, 0.2);
    backdrop-filter: blur( 20.0px );
    -webkit-backdrop-filter: blur( 20.0px );
    border-radius: 10px;
    margin-bottom: calc(5px + (15 - 5) * ((100vw - 320px)/ (1440 - 320)));
}
.img {
    object-fit: cover;
    margin-right: calc(2px + (5 - 2) * ((100vw - 320px)/ (1440 - 320)));
    border-radius: 50%;
    width: calc(35px + (153 - 35) * ((100vw - 320px)/ (1440 - 320)));
    height: calc(35px + (153 - 35) * ((100vw - 320px)/ (1440 - 320)));
}
.title {
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 13ch;
    color: #0E1111;
    font-size: calc(12px + (37 - 12) * ((100vw - 320px)/ (1440 - 320)));
}
.time {
    overflow: hidden;
    max-height: 2ch;
    max-width: 22ch;
    margin-top: -5px;
    color:#0E1111;
    font-size: calc(9px + (16 - 9) * ((100vw - 320px)/ (1440 - 320)));
}
.description{
    max-width: 95%;
    color:#0E1111;
    font-size: calc(6px + (17 - 6) * ((100vw - 320px)/ (1440 - 320)));
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 4; /* number of lines to show */
}

.card:hover{
    box-shadow: 0px 0px 30px rgba(14, 17, 17, 0.7);
}

.clockSvg{
    position: absolute;
    margin-top:calc(3px + (3 - 3) * ((100vw - 320px)/ (1440 - 320))) ;
    width: calc(10px + (14 - 10) * ((100vw - 320px)/ (1440 - 320)));
    margin-left: 2px;
}
.topInfo{
    display: flex;
    justify-content: space-between;
}
.author{
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 1;
    margin-right: 5px;
    padding-right: 5px;
    border-right: 2px solid #0E1111;
}
.rowInfo{
    align-items: center;
    width: 100%;
    flex-wrap: wrap;
    display: flex;
    text-align: center;
    font-size: calc(9px + (16 - 9) * ((100vw - 320px)/ (1440 - 320)));
}
.saveSvg{
    position: absolute;
    left: 95%;
    top: calc(6% + (5 - 6) * ((100vw - 320px)/ (1440 - 320)));
    width: calc(14px + (24 - 14) * ((100vw - 320px)/ (1440 - 320)));
    height: auto;
    float: right;
    cursor: pointer;
}

@media (max-width: 700px) {
    .card{
        width: 100vw;
        z-index: 10;
        margin-top: calc(5px + (10 - 5) * ((100vw - 320px)/ (1440 - 320)));
        height: calc(100px + (200 - 100) * ((100vw - 320px)/ (1440 - 320)));
    }
    .title {
        font-size: calc(24px + (28 - 24) * ((100vw - 320px)/ (1440 - 320)));
    }
    .time {
        font-size: calc(14px + (16 - 14) * ((100vw - 320px)/ (1440 - 320)));
    }
    .description{
        font-size: calc(9px + (22 - 9) * ((100vw - 320px)/ (1440 - 320)));
    }
    .img {
        margin-top: -1px;
        margin-left: -48px;
        margin-right: 5px;
        width: calc(100px + (200 - 100) * ((100vw - 320px)/ (1440 - 320)));
        height: calc(104px + (200 - 104) * ((100vw - 320px)/ (1440 - 320)));
    }

}